import { text } from "./animaciones/baffle.js";
import { typedAnimacion } from "./animaciones/typed.js";

document.addEventListener("DOMContentLoaded", () => {
	iniciarLeyenda();
	StReveal();
});

function iniciarLeyenda() {
	// EFECTO TYPED
	typedAnimacion(typed1, typed2);

	// Efecto baffle
	text.start();
	text.reveal(4000);
	// TIMELINE PARA ANIMACIONE GSAP
	let tl = gsap.timeline({ repeat: 3 });
	tl.from("#camaxtli", {
		duration: 3,
		y: -200, //'random(-500,500)',
		scale: 0,
		stagger: 0.5,
	});
}

function StReveal() {
	Reveal.initialize({ slideNumber: "c/t" });
	({
		center: true,
		transition: "linear",
		transitionSpeed: "slow",
		//backgroundTransition: 'slide'
		autoSlide: 5000,
		loop: true,
	});

	// Reveal.initialize({
	// 	autoSlide: 3000,
	// 	loop: true,
	// });
	// data-autoplay
	Reveal.initialize({
		autoPlayMedia: true,
	});

	// Reveal.initialize({
	// 	autoSlide: 1000,
	// 	loop: false,
	// });

	Reveal.on("fragmentshown", (event) => {
		// event.fragment = the fragment DOM element
		let audioLeyendaAnterior = event.fragment.previousSibling.previousSibling;

		if (audioLeyendaAnterior) {
			audioLeyendaAnterior = audioLeyendaAnterior.children[0];
			audioLeyendaAnterior.pause();
			console.log(audioLeyendaAnterior);
		} else {
			return;
		}
	});

	Reveal.on("fragmenthidden", (event) => {
		// event.fragment = the fragment DOM element
		// console.log(event.fragment);
	});
}
